.modal-backdrop.in {
  opacity: 0.05;
}
.alternative {
  color: #F00;
  font-weight: bolder;
  font-size: 16px;
  margin-bottom: 15px;
  text-align: center;
}
.container-admin-acada {
  margin: 50px auto;
}
@media screen and (min-width: 768px) and (max-width: 959px) {
  .container-admin-acada {
    width: 768px;
  }
}
@media screen and (width: 768px) {
  .container-admin-acada {
    margin-left: 10px;
    margin-right: 10px;
  }
}
@media screen and (min-width: 960px) and (max-width: 1239px) {
  .container-admin-acada {
    width: 960px;
  }
}
@media screen and (min-width: 1240px) {
  .container-admin-acada {
    width: 1200px;
  }
}
.legend > .pull-left > a {
  font-size: 25px;
}
.legend a {
  font-family: monospace;
  color: #6A6A6A;
}
.legend a:hover {
  text-decoration: none;
}
.divider {
  border: 1px solid #D0D0D0;
  background-color: #EAEAEA;
  min-height: 38px;
}
.divider > .date-time {
  background-color: #156779;
  border-color: #156779;
  color: #ffffff;
  display: inline-block;
  min-height: 38px;
  padding: 8px;
  font-size: 12px;
}
.session-semester-info {
  margin-top: 30px;
}
.content-area {
  margin-top: 30px;
}
